import { Component, OnInit } from '@angular/core';
import { ZemogaService } from '../../service/zemoga.service'
import { environment } from '../../../environments/environment';


@Component({
    selector: 'app-zemoga-node',
    templateUrl: './zemogaNode.component.html',
    styleUrls: ['./zemogaNode.component.scss']
})

export class ZemogaNodeComponent implements OnInit {
 	data :any;
 
    constructor(private zemogaService: ZemogaService) { }

     ngOnInit() {
		this.consultarServicio(environment.ENDOPOINT_NG);
	}

	async searchTweet (event) {
		if (event.key === "Enter") {
			try{
				this.data= await this.zemogaService.getElement(environment.ENDOPOINT_NG_SEARCH+event.target.value);
				this.data=this.data.data.statuses;
			}catch(e){
				console.log(e);
			}
		}
		
		if (event.key === "Backspace" && (event.target.value.length ==1 || event.target.value.length ==0)) {
	  		this.consultarServicio(environment.ENDOPOINT_NG);
		}
    }
    
    async consultarServicio(url){
		if(url!=''){
	  		try{
				this.data= await this.zemogaService.getElement(url);
				this.data=this.data.data;
			}catch(e){
				console.log(e);
			}
			
		}
	}
}
