import { Component, OnInit,Input } from '@angular/core';
import { ZemogaService } from '../../service/zemoga.service';
import { environment } from '../../../environments/environment';

@Component({
    selector: 'app-zemoga',
    templateUrl: './zemoga.component.html',
    styleUrls: ['./zemoga.component.scss']
})

export class ZemogaComponent implements OnInit {
 	users : any=[];
    tweets: any=[];
    user: any=null;
    message='';
    
    constructor(private zemogaService: ZemogaService) { }

     ngOnInit() {
		
		this.getUsers(environment.ENDPOINT_J_GET_USER);
	}
    
    async getUsers(url){
		this.message='';
		if(url!=''){
	  		try{
				this.users= await this.zemogaService.getElement(url);			
				return this.users;
			}catch(e){
				console.log(e);
			}
		}
	}
	
	async getTweets(user){
		this.message='';
		this.user=user;
		this.tweets=[];
		try{
			this.tweets= await this.zemogaService.getElement(
				new Array(environment.ENDPOINT_J_GET_USER).concat(user.idportfolio).concat(environment.ENDPOINT_J_GET_TWEET).join("/"));		
		}catch(e){
			console.log(e);
		}
	}

	async searchTweet (event) {
		if (event.key === "Enter") {
			try{
				this.tweets= await this.zemogaService.getElement(
					new Array(environment.ENDPOINT_J_GET_USER).concat(this.user.idportfolio).concat(environment.ENDPOINT_J_GET_TWEET).concat(event.target.value).join("/"));
				this.tweets=this.tweets.data.statuses;
			}catch(e){
				console.log(e);
			}
		}
		
		if (event.key === "Backspace" && (event.target.value.length ==1 || event.target.value.length ==0)) {
	  		this.getTweets(this.user);
		}
    }
    
    async saveInfo(){
		this.message='';
		try{
				this.user= await this.zemogaService.updateElement(
					new Array(environment.ENDPOINT_J_GET_USER).concat(this.user.idportfolio).join("/"), this.user);
				this.message='User updated!!!';
			}catch(e){
				this.message='User does not updated';
				console.log(e);
			}
	}
}
